import isEmpty from 'lodash/isEmpty';
import {
    contractGenerator,
    transformer,
    EVAM,
} from '@devsnc/sn-declarative-actions';

const transformDAClientActionToUXEvents = declarativeActions => {
    if (isEmpty(declarativeActions)) return {};

    return (
        contractGenerator(transformer(declarativeActions), {
            modelType: EVAM,
        }) || {}
    );
};

export default transformDAClientActionToUXEvents;
