/* eslint-disable import/prefer-default-export */
import isEmpty from 'lodash/isEmpty';
import isEqual from 'lodash/isEqual';
import isNull from 'lodash/isNull';

import {
    SEARCH_RESULTS_PAGINATION_BUTTON_CLICKED,
    CORRECTED_TERM_SEARCHED,
    SEARCH_EXECUTED,
    SEARCH_RESULTS_DECLARATIVE_ACTION,
    SEARCH_RESULTS_SEARCH_START,
} from './actions';

import signalTracker from './utils/signalTracker';
import {
    SEARCH_EVENT,
    SEARCH_RESULT_EVENT,
    GENIUS_RESULT_EVENT,
    INFO_EVENT,
    CLICK_ACTION,
    RESULT_TYPES,
    GENIUS_RESULT_VIRTUAL_TABLE,
    DECLARATIVE_ACTION_CLICK,
    CATALOG_SOURCE_TABLE,
} from './constants';

import {
    getSearchAnalyticsModel,
    getSearchResultAnalyticsModel,
    getGeniusResultAnalyticsModel,
} from './utils/analyticsModel';

const _getPaginationToken = (
    currentPage,
    updatedPage,
    previousToken,
    nextToken
) => {
    if (currentPage > updatedPage) return previousToken;
    return nextToken;
};

export const handlePaginationButtonClick = ({
    action,
    dispatch,
    properties,
}) => {
    const {
        searchResultMetadata: { previousPageToken, nextPageToken },
        currentPage,
    } = properties;

    const {
        payload: { value },
    } = action;

    dispatch(SEARCH_RESULTS_PAGINATION_BUTTON_CLICKED, {
        paginationToken: _getPaginationToken(
            currentPage,
            value,
            previousPageToken,
            nextPageToken
        ),
    });
};

const _getLabelDescription = model =>
    isEqual(model.table, CATALOG_SOURCE_TABLE)
        ? model.name
        : model.short_description ||
          model.description ||
          model.name ||
          model.file_name ||
          '';

const _getGeniusResultSourceTable = model =>
    model['columns.table'] != null ? model['columns.table'] : model.table;

const _getGeniusResultLabel = model =>
    model['columns.ai_search_teaser_title'] ||
    model['columns.name'] ||
    model['columns.label'];

const _getResultsModel = (searchResults, geniusResults) => {
    const searchResultsModel = searchResults.map(item => {
        const {
            propValues: { model },
        } = item;

        return {
            recordId: model.sysId,
            tableName: model.table,
            resultType: RESULT_TYPES.REGULAR_RESULT,
        };
    });

    const geniusResultsModel = geniusResults.map(item => {
        const {
            propValues: { model },
        } = item;

        return {
            recordId: model.sysId,
            tableName: model.table,
            resultType: RESULT_TYPES.GENIUS_RESULT,
        };
    });

    return [...searchResultsModel, ...geniusResultsModel];
};

const _getSourceOccurrences = searchResults => {
    const results = {};
    if (!isNull(searchResults)) {
        searchResults.forEach(item => {
            const {
                propValues: { model },
            } = item;
            const { table } = model;

            results[table] = (results[table] || 0) + 1;
        });
    }

    return results;
};

const _getSearchResultsPerSource = searchResults => {
    const sources = _getSourceOccurrences(searchResults);
    const resultsPerSource = Object.keys(sources).map(key => ({
        sourceId: key,
        numberOfResults: sources[key],
    }));

    return resultsPerSource;
};

const _trackSearchEventForRelevancy = model => {
    signalTracker.trackEvent(SEARCH_EVENT, INFO_EVENT, {
        searchContextConfigId: model.searchContextConfigId,
        queryTerm: model.queryTerm,
        searchResults: _getResultsModel(
            !isNull(model.searchResults) ? model.searchResults.items : [],
            !isNull(model.geniusResults) ? model.geniusResults.items : []
        ),
        searchResultsPerSource: _getSearchResultsPerSource(
            !isNull(model.searchResults) ? model.searchResults.items : []
        ),
    });
};

const _trackSearchEventForAppsee = (dispatch, model) => {
    const {
        queryTerm,
        matchedQueryIntents,
        matchedGeniusResultIntents,
        responseTimeInMs,
    } = model;

    const analyticsPayload = getSearchAnalyticsModel({
        searchTerm: queryTerm,
        matchedQueryIntents,
        matchedGeniusResultIntents,
        responseTimeInMs,
    });

    dispatch(SEARCH_RESULTS_SEARCH_START, { analyticsPayload });
};

export const handleResultActionClick = ({ action, dispatch, properties }) => {
    const { searchResultMetadata, searchContextConfigId } = properties;
    const {
        payload: { model, type },
    } = action;

    const {
        searchTerm,
        correctedSearchTerm,
        matchedQueryIntents,
        matchedGeniusResultIntents,
    } = searchResultMetadata;

    const fromSearchResultsContainer = true;
    const resultSysId = model.sysId || '';
    const queryTerm = !isNull(correctedSearchTerm)
        ? correctedSearchTerm
        : searchTerm;

    if (!action.payload.fromSearchResultsContainer) {
        if (isEqual(model.table, GENIUS_RESULT_VIRTUAL_TABLE)) {
            const actionPayload = action.payload.action;
            const sourceTable = _getGeniusResultSourceTable(model);
            const labelDescription = _getGeniusResultLabel(model);
            const aisDocId = sourceTable.concat('_', model.sysId);
            const cardType = model.model || ''; // Model is the type of the Genius Card Template

            signalTracker.trackEvent(GENIUS_RESULT_EVENT, INFO_EVENT, {
                searchContextConfigId,
                queryTerm,
                resultSysId,
                labelDescription,
                sourceTable,
                aisDocId,
                cardType,
                actionType: actionPayload.actionType,
                actionId: actionPayload.assignmentId,
            });

            // To Track Analytics through Service Portal
            const analyticsPayload = getGeniusResultAnalyticsModel({
                searchTerm: queryTerm,
                searchContextConfigId,
                aisDocId,
                labelDescription,
                sourceTable,
                matchedQueryIntents,
                matchedGeniusResultIntents,
                cardType,
                actionName: actionPayload.label,
                actionId: actionPayload.assignmentId,
            });

            const geniusResultActionPayload = {
                ...action.payload,
                analyticsPayload,
                fromSearchResultsContainer,
            };

            dispatch(
                SEARCH_RESULTS_DECLARATIVE_ACTION,
                geniusResultActionPayload
            );
        } else if (isEqual(type, DECLARATIVE_ACTION_CLICK)) {
            const labelDescription = _getLabelDescription(model);
            const sourceTable = model.table != null ? model.table : '';
            const sourceUrl = model.url || '';
            const aisDocId =
                model.table && model.sysId
                    ? model.table.concat('_', model.sysId)
                    : '';
            const selectedResultIndex = Number(model.document_index);

            const { payload } = action;
            const {
                action: { name, actionDispatch, actionPayload, assignmentId },
            } = payload;

            const actionData = {
                actionName: name,
                actionDispatched: actionDispatch,
                actionAssignmentId: assignmentId,
                actionPayload,
            };

            signalTracker.trackEvent(SEARCH_RESULT_EVENT, INFO_EVENT, {
                searchContextConfigId,
                queryTerm,
                resultSysId,
                labelDescription,
                sourceTable,
                sourceUrl,
                signalType: CLICK_ACTION,
                signalValue: selectedResultIndex,
                aisDocId,
                actionData,
            });

            // To Track Analytics through Service Portal
            const analyticsPayload = getSearchResultAnalyticsModel({
                searchTerm: queryTerm,
                searchContextConfigId,
                aisDocId,
                labelDescription,
                sourceTable,
                selectedResultIndex,
                matchedQueryIntents,
                matchedGeniusResultIntents,
            });

            const searchResultActionPayload = {
                ...action.payload,
                analyticsPayload,
                fromSearchResultsContainer,
            };
            dispatch(
                SEARCH_RESULTS_DECLARATIVE_ACTION,
                searchResultActionPayload
            );
        } else {
            dispatch(SEARCH_RESULTS_DECLARATIVE_ACTION, action.payload);
        }
    }
};

export const handleTermClick = ({ action, dispatch, properties }) => {
    const { payload } = action;
    const {
        searchResultMetadata: { searchTerm },
    } = properties;

    if (payload.action === CORRECTED_TERM_SEARCHED) {
        dispatch(SEARCH_EXECUTED, { searchTerm });
    }
};

export const handlePropertyChanged = ({ action, dispatch, properties }) => {
    const {
        payload: { name, value, previousValue },
    } = action;

    const {
        searchResultMetadata: {
            searchTerm,
            correctedSearchTerm,
            matchedQueryIntents,
            matchedGeniusResultIntents,
            responseTimeInMs,
        },
        searchContextConfigId,
        geniusResults,
        searchResults,
    } = properties;

    // Tracks Auto-corrected Search Event when the user types a misspelled word (or)
    // Tracks the Search Event for the Search term entered by the user
    if (
        (isEqual(name, 'searchResultMetadata') &&
            !isEqual(value.searchTerm, previousValue.searchTerm) &&
            !isEmpty(searchTerm)) ||
        (isEqual(name, 'searchResultMetadata') &&
            isEqual(value.searchTerm, previousValue.searchTerm) &&
            !isEmpty(searchTerm) &&
            !isEqual(
                value.correctedSearchTerm,
                previousValue.correctedSearchTerm
            ))
    ) {
        const queryTerm = !isNull(correctedSearchTerm)
            ? correctedSearchTerm
            : searchTerm;

        _trackSearchEventForRelevancy({
            queryTerm,
            searchContextConfigId,
            searchResults,
            geniusResults,
        });

        _trackSearchEventForAppsee(dispatch, {
            queryTerm,
            matchedQueryIntents,
            matchedGeniusResultIntents,
            responseTimeInMs,
        });
    }
};
