import isEmpty from 'lodash/isEmpty';
import '@servicenow/now-button';
import { t } from 'sn-translate';

import { CORRECTED_TERM_SEARCHED } from '../actions';

const view = ({ searchTerm, correctedSearchTerm }) => {
    const ariaLabel = t(
        'Showing results for {0}, search instead for {1},',
        correctedSearchTerm,
        searchTerm
    );
    if (!isEmpty(searchTerm) && !isEmpty(correctedSearchTerm))
        return (
            <div className="message-container">
                <span className="message">
                    {t('Showing results for')}{' '}
                    <span className="spellcheck-message__term">
                        {t(correctedSearchTerm)}
                    </span>
                    <br />
                    <now-button-bare
                        hidePadding
                        label={t('Search instead for {0}', searchTerm)}
                        size="lg"
                        append-to-payload={{
                            action: CORRECTED_TERM_SEARCHED,
                        }}
                        configAria={{
                            'aria-label': ariaLabel,
                        }}
                    />
                </span>
            </div>
        );

    return null;
};

export default view;
