import isEmpty from 'lodash/isEmpty';
import { ANALYTICS_EVENTS, ANALYTICS_EVENT_INTENTS } from '../constants';

const _getChildEvents = model => {
    const childEvents = [];
    const queryIntents = isEmpty(model.matchedQueryIntents)
        ? []
        : model.matchedQueryIntents.split(',');
    const geniusResultIntents = isEmpty(model.matchedGeniusResultIntents)
        ? []
        : model.matchedGeniusResultIntents.split(',');

    geniusResultIntents.forEach(intent =>
        childEvents.push({
            EventName: ANALYTICS_EVENT_INTENTS.GENIUS_RESULT_TRIGGERED,
            EventPayload: {
                SearchQuery: model.searchTerm,
                GeniusResultTriggered: intent,
            },
        })
    );

    queryIntents.forEach(intent =>
        childEvents.push({
            EventName: ANALYTICS_EVENT_INTENTS.QUERY_INTENT_TRIGGERED,
            EventPayload: {
                SearchQuery: model.searchTerm,
                QueryIntentTriggered: intent,
            },
        })
    );

    return childEvents;
};

export const getSearchAnalyticsModel = model => {
    const analyticsPayload = {
        parentEvent: {
            EventName: ANALYTICS_EVENTS.SEARCH_START,
            EventPayload: {
                SearchQuery: model.searchTerm,
                ResponseTime: model.responseTimeInMs,
            },
        },
        childEvents: _getChildEvents(model),
    };

    return analyticsPayload;
};

export const getSearchResultAnalyticsModel = model => {
    const analyticsPayload = {
        parentEvent: {
            EventName: ANALYTICS_EVENTS.SEARCH_RESULT_CLICK,
            EventPayload: {
                SearchQuery: model.searchTerm,
                ClickPosition: parseInt(model.selectedResultIndex, 10),
                DocID: model.aisDocId,
                DocumentTitle: model.labelDescription,
                DataSourceName: model.sourceTable,
            },
        },
        childEvents: _getChildEvents(model),
    };

    return analyticsPayload;
};

export const getGeniusResultAnalyticsModel = model => {
    const analyticsPayload = {
        parentEvent: {
            EventName: ANALYTICS_EVENTS.GENIUS_RESULT_CLICK,
            EventPayload: {
                SearchQuery: model.searchTerm,
                GeniusType: model.cardType,
                GeniusResultDocID: model.aisDocId,
                GeniusResultTitle: model.labelDescription,
                ClickedActionLabel: model.actionName,
                ClickedActionLabelID: model.actionId,
            },
        },
        childEvents: _getChildEvents(model),
    };

    return analyticsPayload;
};
