import '@servicenow/now-data-set';
import isEmpty from 'lodash/isEmpty';

import LoadingView from './view/LoadingView';
import SpellCheckMessage from './view/SpellCheckMessage';
import SearchResults from './view/SearchResults';
import GeniusResults from './view/GeniusResults';
import { LOADING_VIEW_RESULT_COUNT } from './constants';

const _isEmpty = data => isEmpty(data) || isEmpty(data.items);

const view = ({ properties }, { dispatch }) => {
    const {
        isLoading,
        searchResultMetadata,
        geniusResults,
        searchResults,
        currentPage,
    } = properties;

    // No need to pass down all_actions since we already build daClientActionContract
    if (!_isEmpty(searchResults)) delete searchResults.all_actions;
    if (!_isEmpty(geniusResults)) delete geniusResults.all_actions;

    const {
        searchTerm,
        correctedSearchTerm,
        previousPageToken,
        nextPageToken,
    } = searchResultMetadata;
    const geniusResultsExist = !_isEmpty(geniusResults);
    const showGeniusResults = isEmpty(previousPageToken) && geniusResultsExist;

    if (isLoading) {
        return <LoadingView resultCount={LOADING_VIEW_RESULT_COUNT} />;
    }

    return (
        <div>
            <SpellCheckMessage
                searchTerm={searchTerm}
                correctedSearchTerm={correctedSearchTerm}
                dispatch={dispatch}
            />
            <GeniusResults data={geniusResults} show={showGeniusResults} />
            <SearchResults
                searchTerm={searchTerm}
                correctedSearchTerm={correctedSearchTerm}
                data={searchResults}
                currentPage={currentPage}
                previousPageToken={previousPageToken}
                nextPageToken={nextPageToken}
                geniusResultsExist={geniusResultsExist}
            />
        </div>
    );
};

export default view;
