import '@servicenow/now-data-row';
import isEmpty from 'lodash/isEmpty';

import { VERTICAL } from '../constants';

const view = ({ data, show }) =>
    !isEmpty(data) && !isEmpty(data.items) && show ? (
        <div className="genius-results-container">
            <now-data-row
                layout={VERTICAL}
                data={data}
                noBox
                viewAllLinkLabel=""
            />
        </div>
    ) : null;

export default view;
