import { Fragment } from '@servicenow/ui-renderer-snabbdom';

const searchResultItem = (
    <div className="placeholder-search-result-card">
        <div className="placeholder-search-result-card-container loading">
            <div className="placeholder-search-results-marker-container">
                <div className="placeholder-search-results-marker" />
            </div>
            <div className="placeholder-search-results-content">
                <div className="search-result-title" />
                <div className="search-result-description -md" />
                <div className="search-result-description -sm" />
            </div>
        </div>
    </div>
);

const searchResultsPlaceholder = resultCount => (
    <section className="placeholder-search-results">
        {new Array(resultCount).fill(searchResultItem).map(card => (
            <div className="placeholder-search-results-item"> {card} </div>
        ))}
    </section>
);

const view = ({ resultCount }) => (
    <Fragment>
        <div className="search-results-container">
            {searchResultsPlaceholder(resultCount)}
        </div>
    </Fragment>
);

export default view;
