import '@servicenow/now-message';
import { t } from 'sn-translate';
import isEmpty from 'lodash/isEmpty';

import noResultsImage from './assets/no-results.png';

const NoResultsView = ({ searchTerm }) => (
    <div className="message-container">
        <span className="message">
            {t('Your search')}
            <span className="no-results-message__term">
                {!isEmpty(searchTerm) ? ` - ${searchTerm} -` : ''}
            </span>
            &nbsp;{t('has no matches')}
        </span>
        <div className="no-results-message__description">
            <span className="no-results-message__description--hint">
                {t('Keep on searching')}
            </span>
            <now-message alignment="vertical">
                <img
                    slot="media"
                    src={noResultsImage}
                    alt={t('No results found for {0}', searchTerm)}
                    className="no-results-message__description--image"
                />
                <div slot="message">
                    <span className="no-results-message__description--header">
                        {t('Tips')}
                    </span>
                    <p className="no-results-message__description--text">
                        {t(
                            'Try entering shorter phrases, or using different words in your query. For example, try "vacation days" instead of "how many days vacation".'
                        )}
                    </p>
                </div>
            </now-message>
        </div>
    </div>
);

export default NoResultsView;
