import '@servicenow/now-data-set';
import '@servicenow/now-pagination-control';
import isEmpty from 'lodash/isEmpty';

import NoResultsView from './NoResultsView';

const view = ({
    searchTerm,
    correctedSearchTerm,
    data,
    currentPage,
    previousPageToken,
    nextPageToken,
    geniusResultsExist,
}) => {
    const hideGoToNext = isEmpty(nextPageToken);
    const hideGoToPrevious = isEmpty(previousPageToken);

    if (!isEmpty(data.items))
        return (
            <div className="search-results-container">
                <now-data-set
                    data={data}
                    toggleView={false}
                    hidePaginationControls
                    hideFilter
                    defaultView="list"
                />
                <div className="search-results-container--pagination">
                    <now-pagination-control
                        hideGoToFirst
                        hideGoToLast
                        hidePageNumbers
                        hidePageSizeControl
                        hideRange
                        hideGoToPrevious={hideGoToPrevious}
                        hideGoToNext={hideGoToNext}
                        selectedPage={currentPage}
                        manageSelectedPage
                    />
                </div>
            </div>
        );

    if (geniusResultsExist) return null;

    return (
        <NoResultsView
            searchTerm={
                isEmpty(correctedSearchTerm) ? searchTerm : correctedSearchTerm
            }
        />
    );
};

export default view;
