import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import isEmpty from 'lodash/isEmpty';
import uniqBy from 'lodash/uniqBy';
import view from './view';
import styles from './scss/index.scss';
import transformDAClientActionToUXEvents from './utils/declarativeAction';
import {
    PAGINATION_BUTTON_CLICKED,
    CORRECTED_TERM_CLICKED,
    DECLARATIVE_ACTION,
} from './actions';
import { INITIAL_PAGE, FIRST_PAGE } from './constants';
import {
    handlePaginationButtonClick,
    handleResultActionClick,
    handleTermClick,
    handlePropertyChanged,
} from './effects';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

/**
 * Seismic component wrapper for genius results and search results.
 *
 * ```jsx
 * <sn-search-results-container
 *      searchResultMetadata={
 *          searchTerm:'ipone',
 *          correctedSearchTerm: 'iPhone',
 *          previousPageToken:"YWRtaW4gYWRtaW4=",
 *          nextPageToken:"YWRtaW4gYWRtaW4=",
 *          matchedQueryIntents:"personFind",
 *          matchedGeniusResultIntents:"personFind, catalogFind",
 *          responseTimeInMs:918
 *      }
 *      geniusResults={
 *          all_actions: [
 *              {
 *                  name: 'navigation',
 *                  actionType: 'uxf_client_action',
 *                  actionDispatch: 'NAVIGATION',
 *                  actionPayload:
 *                      '{\r\n        "table": "{{table}}",\r\n        "sysId": "{{sys_id}}",\r\n        "url": "{{url}}"\r\n}',
 *                  assignmentId: 'd46f400473211010c342d5fdbdf6a7cf',
 *              }
 *          ],
 *          items: [
 *              {
 *                  template: 'sn-search-genius-user-card',
 *                  propValues: {
 *                      identifier: 'San Diego, USA',
 *                      avatar:
 *                       'https://i.pinimg.com/736x/f5/1d/e5/f51de5a7734a755b1d20009bd3441d92.jpg',
 *                      header: 'System Administrator',
 *                      identifierCriticalText: '4:30 pm',
 *                      primaryDescription: 'Software Engineer, Dev - Search UX',
 *                      secondaryDescription: 'Web Interfaces',
 *                      actions: [
 *                          {
 *                              assignmentId: '2477664313aw13455',
 *                              label: 'Email',
 *                          },
 *                          {
 *                              assignmentId: '876543wne23r5654',
 *                              label: 'Call',
 *                          },
 *                      ],
 *                      model: {
 *                          name: 'System Administrator',
 *                          email: 'admin@example.com',
 *                          company: 'ACME Germany',
 *                          home_phone: '8581234567',
 *                          avatar: '',
 *                          time_zone: null,
 *                          photo: '',
 *                          user_name: 'admin',
 *                          mobile_phone: '',
 *                          location: 'Bockenheimer LandstraÃŸe 123, Frankfurt',
 *                          department: 'Web Interfaces',
 *                          sysId: 'fd826bf03710200044e0bfc8bcbe5d30',
 *                          sys_id: 'fd826bf03710200044e0bfc8bcbe5d30',
 *                          table: 'sys_user',
 *                      },
 *                  },
 *              }
 *          ];
 *      }
 *      searchResults={
 *          all_actions: [
 *              {
 *                  name: 'navigation',
 *                  actionType: 'uxf_client_action',
 *                  actionDispatch: 'NAVIGATION',
 *                  actionPayload:
 *                      '{\r\n        "table": "{{table}}",\r\n        "sysId": "{{sys_id}}",\r\n        "url": "{{url}}"\r\n}',
 *                  assignmentId: 'd46f400473211010c342d5fdbdf6a7cf',
 *              }
 *          ],
 *          items: [
 *              {
 *                  template: 'sn-search-result-evam-card',
 *                  propValues: {
 *                      textHeaderLabelOne: 'OneDrive',
 *                      textHeaderLabelTwo: 'Microsoft Word Document',
 *                      title: '<highlight>Conference</highlight> Possibilities 2020',
 *                      summary:
 *                          'Business travel is liskely to be restricted for the remainder of the year 2020 ... There are two different avenues we can explore for <highlight>conference</highlight> attendance: virtual <highlight>conference</highlight> (domestic and interational); and domestic short travel to in-state, limited attendance <highlight>conference</highlight>s ... Approval for travel in these circumstances requires the extra step ...',
 *                      detailLabelSeparator: '|',
 *                      detailLabelOne:
 *                          'https://taco.acmeco/eeportal/eedocs/hr/policy/conf_policy_emea21.doc',
 *                      detailLabelType: 'inline',
 *                      clickAction: {
 *                          name: 'action_name',
 *                      },
 *                      model: {
 *                          sys_id: 'abc',
 *                      },
 *                  },
 *              }
 *          ];
 *      }
 * >
 * ```
 *
 * @seismicElement sn-search-results-container
 * @summary Seismic component wrapper for genius results and search results.
 */
createCustomElement('sn-search-results-container', {
    renderer: { type: snabbdom },
    view,
    styles,
    behaviors: [{ behavior: rtlBehavior }],
    properties: {
        /**
         * This property specifies the metadata related to search results such as search term, pagination tokens and analytics metadata.
         * @type {object}
         */
        searchResultMetadata: {
            default: {},
            schema: {
                type: 'object',
                properties: {
                    searchTerm: { default: '', type: 'string' },
                    correctedSearchTerm: { default: '', type: 'string' },
                    previousPageToken: { default: '', type: 'string' },
                    nextPageToken: { default: '', type: 'string' },
                    matchedQueryIntents: { default: '', type: 'string' },
                    matchedGeniusResultIntents: { default: '', type: 'string' },
                    responseTimeInMs: { default: '', type: 'string' },
                },
            },
        },
        /**
         * Used to indicate that the search results are being fetched, if set to true - the component will show a loading state.
         * @type {boolean}
         */
        isLoading: {
            default: false,
            schema: {
                type: 'boolean',
            },
        },
        /**
         * The data for genius results can be passed in using this property.
         * @type {object}
         */
        geniusResults: {
            default: {},
            schema: {
                type: 'object',
            },
        },
        /**
         * The data for search results can be passed in using this property.
         * @type {object}
         */
        searchResults: {
            default: {},
            schema: {
                type: 'object',
            },
        },
        /**
         * currentPage is used to identify if the previous or next pagination button was clicked;
         * it manages the currentPage property for now-pagination-control component, which
         * also disables/enables the previous/next page buttons.
         * @type {number}
         */
        currentPage: {
            computed({ properties: { searchResultMetadata } }) {
                const {
                    previousPageToken,
                    nextPageToken,
                } = searchResultMetadata;

                if (previousPageToken == null && nextPageToken != null)
                    return INITIAL_PAGE;

                return FIRST_PAGE;
            },
        },
        /**
         * sys_id of the search context configuration.
         * @type {string}
         */
        searchContextConfigId: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * daClientActionContract is an object which contains the metadata needed for dynamic
         * declarative action event mapping in UI Builder.
         * @type {object}
         */
        daClientActionContract: {
            computed({ properties: { searchResults, geniusResults } }) {
                const declarativeActionsOfSearchResults =
                    !isEmpty(searchResults) &&
                    Array.isArray(searchResults.all_actions)
                        ? searchResults.all_actions
                        : [];

                const declarativeActionsOfGeniusResults =
                    !isEmpty(geniusResults) &&
                    Array.isArray(geniusResults.all_actions)
                        ? geniusResults.all_actions
                        : [];

                const allDAs = uniqBy(
                    [
                        ...declarativeActionsOfSearchResults,
                        ...declarativeActionsOfGeniusResults,
                    ],
                    'assignmentId'
                );

                return transformDAClientActionToUXEvents(allDAs);
            },
            selectable: true,
        },
    },
    actionHandlers: {
        [PAGINATION_BUTTON_CLICKED]: {
            effect: handlePaginationButtonClick,
            stopPropagation: true,
        },
        [DECLARATIVE_ACTION]: {
            effect: handleResultActionClick,
            stopPropagation: true,
        },
        [CORRECTED_TERM_CLICKED]: {
            effect: handleTermClick,
            stopPropagation: true,
        },
        [COMPONENT_PROPERTY_CHANGED]: handlePropertyChanged,
    },
    dispatches: {
        /**
         * Dispatched when the pagination buttons are clicked. The payload contains the previous/next pagination token based on what page the user navigates to.
         * @type {object}
         */
        SEARCH_RESULTS_PAGINATION_BUTTON_CLICKED: {
            schema: {
                type: 'object',
            },
        },
        /**
         * Dispatched when the incorrectly spelled term in the spellcheck message is clicked.
         * @type {object}
         */
        SEARCH_EXECUTED: {
            schema: {
                type: 'object',
            },
        },
        /**
         * Dispatched when a regular search result or an action on a genius card is clicked.
         * @type {object}
         */
        SEARCH_RESULTS_DECLARATIVE_ACTION: {
            schema: {
                type: 'object',
            },
        },
    },
});
